import { Avatar, Box, Text } from '@chakra-ui/react';
import React from 'react';

const UserListItem = ({ user, handleClickUser }) => {
	return (
		<Box
			cursor={'pointer'}
			bg={'#E8E8E8'}
			w='100%'
			d='flex'
			alignItems='center'
			color='black'
			px={3}
			py={2}
			mb={2}
			borderRadius='lg'
			_hover={{
        background: "#38B2AC",
        color: "white",
      }}
			onClick={handleClickUser}
		>
			<Avatar
        mr={2}
        size="sm"
        cursor="pointer"
        name={user.name}
        src={user.pic}
      />
			<Box>
        <Text>{user.name}</Text>
        <Text fontSize="xs">
          <b>Email : </b>
          {user.email}
        </Text>
      </Box>
		</Box>
	);
};

export default UserListItem;
